import React from 'react'

const elem = 3

const abbrevs = [
  [/^m /, 'masculine '],
  [/^f /, 'feminine '],
  [/^n /, 'neuter '],
  [/^perf /, 'perfective '],
]

export default ({ nodes }) => {
  if (nodes.length !== 1 || nodes[0].data[elem].length < 1) {
    return null
  }
  let t = nodes[0].data[elem]
  abbrevs.forEach(([expr, repl]) => {
    t = t.replace(expr, repl)
  })
  return <code>{t}</code>
}
